@import "lib/settings";
@import "lib/general";
@import "lib/grid";
@import "lib/font";
@import "theme/header";
@import "theme/footer";
$widthContent: 80rem;
$orange : #C75809;
$yellow  : #F1D100;
$gray : rgb(109, 110, 113);


.ErrorsAlert {
  position: fixed;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.6); }
.ErrorsAlert-content {
  border: 0.25rem solid white;
  background: #ff696d;
  color: white;
  padding: rem(10) rem(46);
  position: relative;
  font-size: rem(18);
  span {
    position: absolute;
    right: rem(-16);
    top: rem(-17);
    border: 0.125rem solid white;
    background: #ff696d;
    padding: rem(14) rem(9);
    font-size: rem(18);
    border-radius: 50%;
    line-height: 0;
    font-weight: 300;
    cursor: pointer; } }

main {
  max-width: $widthContent;
  margin: 2rem auto;
  position: relative; }
body {
  color: $greenDark;
  font-size: .875rem; }
.Form-register {
  max-width: 50rem;
  margin: 1rem auto;
  font-size: rem(16);
  color: $gray;
  padding: 0 rem(20);
  .result {
    position: absolute;
    top: 0;
    left: 0;
    max-height: 100%;
    overflow: scroll; }
  figure {
    font-size: rem(12);
    text-align: center;
    .error {
      position: absolute;
      right: 0;
      color: red;
      font-size: rem(15);
      top: rem(-22);
      margin: 0; } }
  button {
    padding: rem(10) rem(50);
    background: #67b9e5;
    margin: rem(20) 0;
    color: white;
    font-size: rem(14); }
  @include respond-to(medium) {
    padding: 0;
    margin: 9rem auto; }
  label {
    margin: rem(6) 0;
    em {
      font-size: rem(12); }
    p {
      margin: rem(4) 0 rem(8);
      text-align: right;
      width: 100%;
      color: red;
      font-size: rem(14);
      & ~ input,& ~ select,& ~ textarea {
        border: rem(2) red solid; } } }
  input, select, textarea {
    border: rem(2) $gray solid;
    height: rem(32);
    width: 100%;
    padding: 0 rem(4);
    font-size: rem(14);
    &.error {
      border: rem(2) red solid; } }
  textarea {
    height: rem(150);
    padding-top: rem(4); }
  h1 {
    font-size: 1.4rem;
    font-weight: 300;
    color: $gray;
    @include respond-to(medium) {
      font-size: 1.8rem; }
    b {
      display: block;
      font-weight: bold; } } }

#image-profile {
  min-height: 100%;
  width: 100%;
  opacity: 0;
  cursor: pointer; }
.Form-figure-user {
  border: rem(2) solid $gray;
  height: rem(180);
  max-width: rem(180);
  cursor: pointer;
  position: relative; }
.Form-contentImageUser {
  margin-top: rem(15);
  @include respond-to(medium) {
    padding: 0 0 0 rem(20); }
  div {
    margin-bottom: rem(7); } }
.Form-hr {
  border: none;
  height: rem(2);
  background: $gray;
  margin: rem(16) 0; }


.select2-container--default .select2-selection--multiple, .select2-selection--multiple, .select2-selection--multiple {
  border: 2px solid #6d6e71 !important;
  border-radius: 0 !important; }
